:root {
  --bg: #ffffff;
  --text: #1f2937;
  --muted: #6b7280;
  --brand: #111827;
  --accent: #2563eb;
  --surface: #f8fafc;
  --border: #e5e7eb;
  --radius: 12px;
}

* { box-sizing: border-box; }
html, body {
  margin: 0; padding: 0;
  background: var(--bg); color: var(--text);
  font: 16px/1.65 -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, "PingFang SC", "Microsoft Yahei", sans-serif;
}
img, video, iframe { max-width: 100%; display: block; }
a { color: inherit; }

.container { width: min(1100px, 92%); margin: 0 auto; }
.narrow   { width: min(900px, 92%);  margin: 0 auto; }

/* Header：半透明、粘顶 */
.site-header {
  position: sticky; top: 0; z-index: 10;
  backdrop-filter: saturate(1.5) blur(8px);
  background: rgba(255,255,255,.85);
  border-bottom: 1px solid var(--border);
}
.header-inner { display: flex; align-items: center; justify-content: space-between; height: 56px; }
.brand { font-weight: 700; color: var(--brand); }

/* Header 右上角按钮（深色胶囊） */
.nav.actions { display: flex; align-items: center; gap: 12px; }
.pill {
  display: inline-flex; align-items: center; justify-content: center;
  height: 36px; padding: 0 16px;
  border-radius: 999px;
  background: #2b2b2b; color: #fff; text-decoration: none;
  border: 1px solid rgba(255,255,255,.06);
  transition: transform .04s ease, filter .2s ease, background .2s ease;
  font-weight: 500; letter-spacing: .2px;
}
.pill:hover { filter: brightness(1.1); }
.pill:active { transform: translateY(1px); }
.pill.demo { background: #2b2b2b; }
@media (max-width: 520px) {
  .nav.actions { gap: 8px; }
  .pill { height: 34px; padding: 0 12px; font-size: 14px; }
}

/* Sections */
.section { padding: 56px 0; }
.section h2 { margin: 0 0 16px; font-size: 1.6rem; }
.hint, .subtle { color: var(--muted); font-size: .95rem; }
.subtle { margin-top: -6px; margin-bottom: 14px; }

/* Hero（标题区） */
.hero { padding-top: 72px; }
.hero h1 { font-size: clamp(1.8rem, 3vw, 2.6rem); margin: 0 0 8px; }
.authors, .affiliations { color: var(--muted); margin: 6px 0; }

/* 按钮（正文中） */
.cta { margin: 16px 0 20px; display: flex; flex-wrap: wrap; gap: 10px; }
.btn {
  display: inline-block; padding: 10px 14px;
  border: 1px solid var(--border); border-radius: 8px;
  text-decoration: none; color: var(--text); background: var(--surface);
}
.btn.primary { background: var(--accent); color: #fff; border-color: var(--accent); }
.btn:hover { filter: brightness(.97); }

/* 卡片 */
.card { border: 1px solid var(--border); border-radius: var(--radius); overflow: hidden; background: #fff; }

/* 浅灰区（Abstract、Impact） */
.section.gray { background: #f3f4f6; }
.lead {
  width: min(900px, 92%);
  margin: 0 auto;
  font-size: 18px; line-height: 1.85; color: var(--text);
}
.h2-center { text-align: center; font-size: 2rem; margin-bottom: 20px; }
.section.tight { padding-top: 44px; padding-bottom: 44px; }

/* 图墙（Results） */
.gallery { display: grid; grid-template-columns: repeat(auto-fill, minmax(240px, 1fr)); gap: 12px; }
.gallery .card img { width: 100%; height: auto; display: block; }

/* 代码块（用于 BibTeX 等） */
pre.code {
  background: #0b1020; color: #e6edf3; padding: 16px;
  border-radius: var(--radius); overflow: auto;
  font-family: ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace;
}

/* 页脚 */
.site-footer { border-top: 1px solid var(--border); background: var(--surface); }
.footer-inner { display: flex; align-items: center; justify-content: center; gap: 10px; height: 64px; color: var(--muted); }
.sep { opacity: .6; }

/* Lightbox */
.lightbox { position: fixed; inset: 0; background: rgba(0,0,0,.75); display: none; align-items: center; justify-content: center; padding: 24px; }
.lightbox img { max-width: min(1000px, 94vw); max-height: 90vh; border-radius: 8px; }
.lightbox.show { display: flex; }

/* ===== Hero 轮播样式（修复 .hero-slider 断行） ===== */
.hero-slider { position: relative; background: #000; }
.hero-slider .slides {
  position: relative;
  aspect-ratio: 21 / 9; /* 需要可改成 16 / 9 */
  overflow: hidden;
}
.hero-slider .slide {
  position: absolute; inset: 0;
  opacity: 0; transition: opacity .45s ease;
  pointer-events: none;
}
.hero-slider .slide.is-active { opacity: 1; pointer-events: auto; }
.hero-slider img { width: 100%; height: 100%; object-fit: cover; display: block; }

/* 左右切换按钮 */
.hero-slider .nav {
  position: absolute; top: 50%; transform: translateY(-50%);
  width: 40px; height: 40px; border: 0; border-radius: 999px;
  background: rgba(0,0,0,.45); color: #fff; cursor: pointer;
  display: flex; align-items: center; justify-content: center;
  backdrop-filter: blur(2px);
}
.hero-slider .nav:hover { background: rgba(0,0,0,.6); }
.hero-slider .nav.prev { left: 10px; }
.hero-slider .nav.next { right: 10px; }

/* 圆点 */
.hero-slider .dots {
  position: absolute; left: 50%; bottom: 12px; transform: translateX(-50%);
  display: flex; gap: 8px;
}
.hero-slider .dot {
  width: 8px; height: 8px; border-radius: 999px;
  background: rgba(255,255,255,.6); border: 0; padding: 0; cursor: pointer;
}
.hero-slider .dot.is-active { background: #fff; }

@media (max-width: 640px) {
  .hero-slider .slides { aspect-ratio: 16 / 9; }
  .hero-slider .nav { width: 34px; height: 34px; }
}

/* 移动端：PDF 预览比例 */
@media (max-width: 480px) {
  .pdf-frame { aspect-ratio: 3 / 4; }
}
